@font-face {
  font-family: "AvenirNext";
  font-style: normal;
  font-weight: medium;
  src: url("../fonts/AvenirNext/AvenirNextLTPro-MediumCn.eot?#iefix") format("embedded-opentype"), url("../fonts/AvenirNext/AvenirNextLTPro-MediumCn.woff2") format("woff2"), url("../fonts/AvenirNext/AvenirNextLTPro-MediumCn.woff") format("woff"), url("../fonts/AvenirNext/AvenirNextLTPro-MediumCn.ttf") format("truetype"), url("../fonts/AvenirNext/AvenirNextLTPro-MediumCn.svg#AvenirNext") format("svg"); }

.wrapper-block {
  width: 964px;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto; }
  .wrapper-block:after {
    content: " ";
    display: block;
    clear: both; }

.row {
  margin-left: -15px;
  margin-right: -15px; }
  .row::after {
    clear: both;
    content: "";
    display: table; }

h1 {
  font-size: 48px;
  line-height: 36px;
  font-weight: 300;
  margin-bottom: 40px; }

h2 {
  font-size: 30px;
  line-height: 36px;
  font-weight: 300;
  margin-bottom: 25px; }

h3 {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  margin-bottom: 30px; }

h4 {
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
  margin-bottom: 20px; }

* {
  box-sizing: border-box; }

.wrapper {
  position: relative; }

body {
  font-family: 'AvenirNext', Arial, sans-serif;
  font-size: 16px; }

a {
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out; }

.head-site .main-menu {
  height: 40px;
  line-height: 40px;
  background: #c90f0f; }
  .head-site .main-menu .city {
    width: 16.66667%;
    float: left;
    padding-left: 1.66667%;
    padding-right: 1.66667%;
    padding-left: 15px;
    padding-right: 15px; }
    .head-site .main-menu .city .city__current {
      color: #fe9389; }
      .head-site .main-menu .city .city__current .city__current__location {
        padding-right: 5px; }
      .head-site .main-menu .city .city__current .city__current--set {
        position: relative; }
        .head-site .main-menu .city .city__current .city__current--set:after {
          line-height: 1;
          display: block;
          position: absolute;
          top: 0;
          left: 100%;
          content: "\f107";
          margin-left: 5px;
          font-family: "FontAwesome";
          -webkit-transition: all ease-in-out 0.2s;
          -moz-transition: all ease-in-out 0.2s;
          transition: all ease-in-out 0.2s; }
      .head-site .main-menu .city .city__current.active .city__current--set:after {
        -webkit-transform: rotate(180deg);
        -moz-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        -o-transform: rotate(180deg);
        transform: rotate(180deg); }
    .head-site .main-menu .city .city__list {
      display: none; }
  .head-site .main-menu .navigation {
    width: 66.66667%;
    float: left;
    padding-left: 1.66667%;
    padding-right: 1.66667%;
    padding-left: 15px;
    padding-right: 15px; }
    .head-site .main-menu .navigation .navigation__ul {
      height: 40px;
      display: table; }
      .head-site .main-menu .navigation .navigation__ul .navigation__list {
        display: table-cell;
        padding-left: 8px;
        padding-right: 8px; }
        .head-site .main-menu .navigation .navigation__ul .navigation__list .navigation__link {
          text-decoration: none;
          font-size: 16px;
          display: block;
          color: #fe9389; }
  .head-site .main-menu .phone {
    width: 16.66667%;
    float: left;
    padding-left: 1.66667%;
    padding-right: 1.66667%;
    padding-left: 15px;
    padding-right: 15px;
    text-align: right; }
    .head-site .main-menu .phone a {
      text-decoration: none;
      font-size: 18px;
      color: #fe9389; }

.head-site .info-menu-block {
  background: #eb2626;
  padding-top: 19px;
  padding-bottom: 19px; }
  .head-site .info-menu-block::after {
    clear: both;
    content: "";
    display: table; }
  .head-site .info-menu-block .logo {
    margin-right: 50px;
    float: left;
    height: 40px; }
    .head-site .info-menu-block .logo a {
      display: block; }
  .head-site .info-menu-block .info-menu__ul {
    float: left;
    font-size: 0; }
    .head-site .info-menu-block .info-menu__ul .info-menu__list {
      display: inline-block;
      padding-left: 50px; }
      .head-site .info-menu-block .info-menu__ul .info-menu__list:first-child {
        padding-left: 0; }
      .head-site .info-menu-block .info-menu__ul .info-menu__list .info-menu__link {
        font-size: 20px;
        color: #fff;
        text-decoration: none;
        display: block;
        line-height: 40px; }
  .head-site .info-menu-block .user-block {
    float: right;
    font-size: 0; }
    .head-site .info-menu-block .user-block .user-block__search {
      margin-right: 30px;
      display: inline-block; }
      .head-site .info-menu-block .user-block .user-block__search a {
        text-decoration: none;
        color: #fff;
        font-size: 24px;
        line-height: 40px; }
    .head-site .info-menu-block .user-block .user-block__enter {
      display: inline-block; }
      .head-site .info-menu-block .user-block .user-block__enter .btn-enter {
        text-decoration: none;
        display: block;
        height: 40px;
        line-height: 36px;
        border: 2px solid #fff;
