        text-align: center;
        font-size: 18px;
        padding: 0 15px;
        border-radius: 20px; }

.action-block {
  background: #ecdfd3;
  color: #000;
  padding-top: 50px; }
  .action-block .action-slider .action-slider__item {
    height: 500px;
    position: relative; }
    .action-block .action-slider .action-slider__item .action-slider__img {
      position: absolute;
      top: 0;
      right: 0; }
    .action-block .action-slider .action-slider__item .action-slider__validity {
      font-size: 20px;
      margin-bottom: 80px; }
    .action-block .action-slider .action-slider__item .action-slider__name {
      word-spacing: 10000px;
      font-size: 50px;
      line-height: 1;
      margin-bottom: 80px; }
    .action-block .action-slider .action-slider__item .action-slider__discount {
      position: absolute;
      bottom: -55px;
      right: 0;
      height: 200px;
      width: 200px;
      border-radius: 50%;
